import React from "react";
import styled from "styled-components";

const Container = styled.div`
  margin-top: 20px;
`;

const BoxWrapper = styled.div`
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 10px 0;
`;

const Title = styled.h2`
  text-align: left;
  margin: 0;
  padding: 0;
`;

const HistorySection = styled.div`
  margin: 20px 0;
  width: 100%;
  height: 400px;
  gap: 5%;
`;

const PicturesSection = styled.div`
  row-gap: 0px;
  column-gap: 30px;
  padding: 20px;

  img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    box-shadow: 0 0 10px 5px #666;
  }
  img:nth-child(even) {
    transform: translateY(70px); /* 짝수 번째 이미지 아래로 이동 */
  }
`;

const DogInfoSection = styled.div`
  flex: 1;
  padding: 0, 20px;
  width: 100%;
  height: 100%;
`;

const historyDescription = `  뽀삐는 어미에게 버림받고 거리에서 상처 입은 채 살아가던 유기견입니다. 
  구조되었지만 <strong>보호소의 재정 부족</strong>으로 인해 <strong>충분한 치료</strong>를 받기 어려웠습니다. 
  특히 <strong>다리 수술비가 부족</strong>해 <strong>생명에 위협</strong>을 받고 있었습니다. 
  <strong>후원이 없으면</strong> 유기견들은 치료를 받지 못하고, 결국 죽음에 이를 수 있습니다. 
  <strong>작은 후원이 그들의 생명을 살릴 수 있는 절실한 도움</strong>이 됩니다.
`;

const images = [
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg", // 실제 이미지 경로로 변경
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
  "https://i.ytimg.com/vi/W99uYn4qVTY/maxresdefault.jpg",
];

const history = () => {
  return (
    <Container className="flex flex-column">
      <BoxWrapper className="flex w-100">
        <Title className="w-100">뽀삐의 History</Title>
      </BoxWrapper>
      <HistorySection className="flex flex-row">
        <PicturesSection className="pictures grid grid-cols-2 w-half">
          {images.map((src, index) => (
            <img key={index} src={src} alt={`image-${index}`} />
          ))}
        </PicturesSection>

        <DogInfoSection className="flex flex-column align-start text-left justify-center">
          <h3>뽀삐</h3>
          <p>나이: 1세</p>
          <p dangerouslySetInnerHTML={{ __html: historyDescription }}></p>
        </DogInfoSection>
      </HistorySection>
    </Container>
  );
};
export default history;
