import React from "react";
import { Box, Typography, Button, Grid2, Card, CardMedia } from "@mui/material";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import ShareIcon from "@mui/icons-material/Share";
import Categories from "./Categories";

import MainImage from "./MainImage";

const DogInfo = () => {
  const infoItems = [
    { label: "달성률", value: "223%", extra: "목표금액 50,002,000원" },
    { label: "남은기간", value: "10일", extra: "2024-10-02에 종료" },
    { label: "투자자수", value: "41명" },
  ];

  const categories = ["채권형", "소득공제가능", "선착순"]; // '채권형', '소득공제가능', '선착순' 카테고리
  const title = "증액 예정 | 소득공제 | 전환 사채 주거 구독";
  const imgSrc =
    "//image-se.ycrowdy.com/20240909/CROWDY_202409091318250233_oSBCe.jpg/ycrowdy/resize/!740x!417";

  const WarningMessage = () => {
    return (
      <Typography
        variant="caption"
        sx={{
          marginTop: 2,
          display: "block",
          color: "gray",
          borderRadius: "6px",
          backgroundColor: "#fef1f1",
          fontSize: "12px",
          padding: "5px 12px",
          lineHeight: "18px",
          textAlign: "center",
        }}
      >
        크라우드펀딩 투자는 <b>투자금액 전부</b>를 잃을 수 있는 <b>높은 위험</b>
        을 가지고 있습니다. 투자위험에 대해 이해가 있는 경우에만 투자에
        참여하세요.
      </Typography>
    );
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: 2,
        gap: 4,
      }}
    >
      {/* 왼쪽 영역 */}
      <Box sx={{ flex: 2, marginRight: 2 }}>
        <Typography
          variant="h6"
          fontSize={"32px"}
          fontWeight={"bold"}
          letterSpacing={"-1px"}
          lineHeight={"50px"}
        >
          {title}
        </Typography>

        {/* 이미지 영역 */}
        <MainImage imgSrc={imgSrc} />
      </Box>

      {/* 오른쪽 영역 */}
      <Box sx={{ flex: 1, alignContent: "center" }}>
        {/* 카테고리 텍스트 */}
        <Categories categories={categories} />

        {/* 펀딩 금액 */}
        <div className="flex">
          <Typography variant="h4" component="span" sx={{ fontWeight: "bold" }}>
            111,527,000원
          </Typography>
          <Typography variant="body1" component="span">
            {" "}
            펀딩중
          </Typography>
        </div>

        {/* 달성률, 남은기간, 투자자수 등 */}
        <Grid2 container spacing={2} sx={{ marginTop: 2 }}>
          {infoItems.map((item, index) => (
            <Grid2 key={index} xs={12} sm={6}>
              <Typography variant="body2">
                {item.label}: <strong>{item.value}</strong>{" "}
                {item.extra && `| ${item.extra}`}
              </Typography>
            </Grid2>
          ))}

          {/* 버튼들 */}
          <Grid2 xs={12}>
            <Button
              variant="outlined"
              sx={{ marginRight: 1 }}
              startIcon={<FavoriteBorderIcon />}
            >
              17명이 관심있어요
            </Button>
            <Button variant="outlined" startIcon={<ShareIcon />}>
              공유하기
            </Button>
          </Grid2>
        </Grid2>

        {/* 경고 메시지 */}
        <WarningMessage />
      </Box>
    </Box>
  );
};

export default DogInfo;
