import * as React from 'react';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import RestaurantIcon from '@mui/icons-material/Restaurant'; // 식품 아이콘
import ToysIcon from '@mui/icons-material/Toys'; // 장난감 아이콘
import ChairIcon from '@mui/icons-material/Chair'; // 가구 아이콘
import Stroller from '@mui/icons-material/Stroller'; // 개모차 아이콘
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'; // 장바구니 아이콘
import GavelIcon from '@mui/icons-material/Gavel'; // 실시간 경매 아이콘
import {useNavigate} from "react-router-dom";

export default function Sidebar() {
    const navigate = useNavigate();
    // 각 그룹별 메뉴 항목 배열
    const menuItemsGroup1 = [
        {text: '식품', icon: <RestaurantIcon sx={{color: 'black'}}/>},
        {text: '장난감', icon: <ToysIcon sx={{color: 'black'}}/>},
        {text: '가구', icon: <ChairIcon sx={{color: 'black'}}/>},
        {text: '개모차', icon: <Stroller sx={{color: 'black'}}/>},
    ];
    const menuItemsGroup2 = [
        {text: '장바구니', icon: <ShoppingCartIcon sx={{color: 'black'}}/>, link: '/cart'}
    ];
    const menuItemsGroup3 = [
        {text: '실시간 경매', icon: <GavelIcon sx={{color: 'black'}}/>},
    ];

    return (
        <Box sx={{display: 'flex', zIndex: 1000}}>
            <Toolbar/>
            {/* 내부 콘텐츠를 하나의 박스로 감싸기 */}
            <Box
             sx={{
             position: 'fixed', // 사이드바를 고정 위치로 설정
             top: '70px', // 원하는 위치에 맞게 top 조정
             left: '50px', // 원하는 위치에 맞게 left 조정
             borderRadius: '10px',
             boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
             border: '1px solid #e0e0e0',
             padding: '10px',
             backgroundColor: 'white',
             width: '250px',
             height: 'calc(100vh - 130px)', // 화면 높이에 맞춰 조정
             overflowY: 'auto', // 사이드바 내부 스크롤 추가
             // marginLeft: '100px', // 고정된 위치에서 margin은 필요하지 않음
             // marginTop: '100px',  // 고정된 위치에서 margin은 필요하지 않음
             }}

            >
                {/* 첫 번째 드롭다운 카테고리 */}
                <Accordion
                    disableGutters
                    elevation={0}
                    square
                    sx={{
                        backgroundColor: 'white',
                        '&:before': {display: 'none'},
                        color: 'black',
                    }}
                >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon sx={{color: 'black'}}/>}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <ListItemText primary="카테고리" sx={{color: 'black'}}/>
                    </AccordionSummary>
                    <AccordionDetails sx={{padding: 2}}>
                        <List sx={{padding: 0}}>
                            {menuItemsGroup1.map((item) => (
                                <ListItem key={item.text} disablePadding>
                                    <ListItemButton sx={{padding: '8px 16px'}}>
                                        {/* 각 카테고리의 아이콘 추가 */}
                                        <ListItemIcon>
                                            {item.icon}
                                        </ListItemIcon>
                                        <ListItemText primary={item.text} sx={{color: 'black'}}/>
                                    </ListItemButton>
                                </ListItem>
                            ))}
                        </List>
                    </AccordionDetails>
                </Accordion>

                <Divider/>

                {/* 두 번째 그룹 */}
                <List sx={{paddingBottom: 0}}>
                    {menuItemsGroup2.map((item) => (
                        <ListItem key={item.text} disablePadding>
                            <ListItemButton sx={{padding: '8px 16px'}} onClick={() => {
                                navigate(item.link)
                            }}>
                                <ListItemIcon>
                                    {item.icon}
                                </ListItemIcon>
                                <ListItemText primary={item.text} sx={{color: 'black'}}/>
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>

                <Divider/>

                {/* 세 번째 그룹 */}
                <List sx={{paddingBottom: 0}}>
                    {menuItemsGroup3.map((item) => (
                        <ListItem key={item.text} disablePadding>
                            <ListItemButton sx={{padding: '8px 16px'}}>
                                <ListItemIcon>
                                    {item.icon}
                                </ListItemIcon>
                                <ListItemText primary={item.text} sx={{color: 'black'}}/>
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </Box>
        </Box>
    );
}
