import React, { useState } from "react";
import magnifyingGlassIcon from "../../static/magnifyingGlass.png"; // 돋보기 이미지 경로
import "./css/BoardSearchBox.css";

const BoardSearchBox = () => {
  const [keyword, setKeyword] = useState("");

  const handleSearch = (event) => {
    event.preventDefault();
    // 검색 로직을 추가하거나 키워드를 전달
    console.log("검색어:", keyword);
  };

  return (
    <div className="board-search-container">
      <form onSubmit={handleSearch} className="board-search-form">
        <input
          type="text"
          placeholder="제목으로 검색"
          value={keyword}
          onChange={(e) => setKeyword(e.target.value)}
          className="board-search-input"
        />
        <button type="submit" className="board-search-button">
          <img
            src={magnifyingGlassIcon}
            alt="검색"
            className="board-search-icon"
          />
        </button>
      </form>
    </div>
  );
};

export default BoardSearchBox;
